import request from '@/utils/request'

// 工单汇总信息
export const taskReportInfo = (start, end) =>
  request.get(`/api/task-service/task/taskReportInfo/${start}/${end}`)
// 工单搜索下拉框选项
export const getworkstatus = () =>
  request.get('/api/task-service/task/allTaskStatus')
// 工单汇总信息
export const getworktask = ({
  isRepair = '',
  status = '',
  taskCode = '',
  pageIndex = 1
}) =>
  request.get('/api/task-service/task/search/', {
    params: {
      isRepair: isRepair,
      status: status,
      pageIndex: pageIndex,
      taskCode: taskCode
    }
  })
// 工单补货预警
export const supplyAlertValue = () =>
  request.get('/api/task-service/task/supplyAlertValue')
// 设置工单补货预警
export const supplyConfigValue = (data) =>
  request.post('/api/task-service/task/autoSupplyConfig', {
    alertValue: data
  })
// 设置工单类型
export const supplySelectList = (data) =>
  request.get('/api/task-service/taskType/list', {
    params: { alertValue: data }
  })

// 获取经营人员
export const Inputtask = (data) =>
  request.get(`/api/user-service/user/operatorList/${data}`)

// 创建工单
export const AddtaskService = (data) =>
  request.post('/api/task-service/task/create', data)
// 获取售货机货道详情
export const ChannelList = (data = 0) =>
  request.get(`/api/vm-service/channel/channelList/${data}`)
// 获取详情
export const TaskInfo = (id) =>
  request.get(`/api/task-service/task/taskInfo/${id}`)
// 取消工单
export const Taskcancel = (id) =>
  request.post(`/api/task-service/task/cancel/${id}`, {
    taskId: id,
    desc: ''
  })
